import React from 'react';
import PropTypes from 'prop-types';
import MUIDataTable from 'mui-datatables';
import { ThemeProvider } from '@material-ui/core';
import customizedTheme from './customizedTheme';

const MaterialTable = props => {
  const { columns, data, options, toolbarNarrow = false, ...rest } = props;

  const defaultOptions = {
    filterType: 'dropdown',
    selectableRows: 'none',
    download: false,
    print: false,
    sortFilterList: false,
    viewColumns: false,
    elevation: 0,
    textLabels: {
      body: {
        noMatch: 'Xin li, khng tm thy d liu  hin th',
        toolTip: 'Sp xp',
        columnHeaderTooltip: column => `Sp xp theo ${column.label}`
      },
      filter: {
        all: 'Tt c',
        title: 'B LC',
        reset: 't li'
      },
      toolbar: {
        search: 'Tm kim',
        filterTable: 'B lc',
        viewColumns: 'Hin th ct'
      },
      pagination: {
        next: 'Trang k tip',
        previous: 'Trang trc',
        rowsPerPage: 'S hng mi trang:',
        displayRows: 'trong'
      }
    },
    ...options
  };

  return (
    <ThemeProvider theme={customizedTheme(toolbarNarrow)}>
      <MUIDataTable
        data={data}
        columns={columns}
        options={defaultOptions}
        {...rest}
      />
    </ThemeProvider>
  );
};

MaterialTable.propTypes = {
  columns: PropTypes.array,
  data: PropTypes.array,
  options: PropTypes.object,
  toolbarNarrow: PropTypes.bool
};

export default MaterialTable;
