import validate from 'validate.js';

const validators = {
  checked: (value, options) => {
    if (value !== true) {
      return options.message || 'phi c xc nhn';
    }
    return true;
  }
};

validate.validators = {
  ...validate.validators,
  ...validators
};

validate.validators.email.message = 'khng hp l';

export default validate;
