const addNewUserForm = [
  { label: 'Email', name: 'email', type: 'email', isRequired: true },
  { label: 'Mt khu', name: 'password', type: 'password', isRequired: true },
  {
    label: 'Xc nhn mt khu',
    name: 'confirmPassword',
    type: 'password',
    isRequired: true
  },
  {
    label: 'M s nhn vin',
    name: 'employeeId',
    type: 'text',
    isRequired: true
  },
  { label: 'Tn', name: 'firstName', type: 'text', isRequired: true },
  { label: 'H v Tn lt', name: 'lastName', type: 'text', isRequired: true },
  { label: 'S in thoi', name: 'phone', type: 'number', isRequired: true }
];

/*eslint-disable react/no-multi-comp */
const UsersDialog = props => {
  const { open, dialogType, onCloseDialog } = props;
  const [signUp, { loading, error }] = useMutation(SIGN_UP);

  const handleCreateNewUser = async userInfo => {
    console.log(userInfo);
    // await signUp({ variables: userInfo });
  };

  const switchDialog = dialogType => {
    switch (dialogType) {
      case 'addNewUserForm':
        return (
          <FormDialog
            open={open}
            handleContinue={handleCreateNewUser}
            handleCancel={onCloseDialog}
            title={'Thm ngi dng'}
            formData={addNewUserForm}
            error={localizeError(error)}
            isLoading={loading}
          />
        );
      default:
        return null;
    }
  };

  return switchDialog(dialogType);
};

UsersDialog.propTypes = {
  open: PropTypes.bool,
  dialogType: PropTypes.string,
  onCloseDialog: PropTypes.func
};

export default UsersDialog;
