import React from 'react';
import PropTypes from 'prop-types';
import { FormDialog, AlertDialog } from 'src/components';
import _ from 'lodash';
import Image from 'material-ui-image';
import { localizeError } from 'src/helpers';
import { useMutation } from 'react-apollo';
import { ADD_DEVICE } from 'src/utils/graphqlMutations';

const addNewDeviceForm = [
  { label: 'Tn thit b', name: 'title', type: 'text', isRequired: true },
  { label: 'K hiu thit b', name: 'model', type: 'text', isRequired: true },
  {
    label: 'Cng ty sn xut',
    name: 'manufacturer',
    type: 'text',
    isRequired: true
  },
  {
    label: 'Nc sn xut',
    name: 'origin',
    type: 'text',
    isRequired: true
  },
  {
    label: 'Nm sn xut',
    name: 'manufacturedYear',
    type: 'year',
    isRequired: false
  },
  {
    label: 'Thi im a vo s dng',
    name: 'startUseTime',
    type: 'date',
    isRequired: false
  },
  {
    label: 'Tnh trng a vo s dng',
    name: 'startUseState',
    type: 'select',
    isRequired: false
  },
  {
    label: 'Gi thnh',
    name: 'originalPrice',
    type: 'number',
    isRequired: true
  },
  {
    label: 'B phn',
    name: 'faculty',
    type: 'text',
    isRequired: true
  }
];
const areEqual = (prevProps, nextProps) => _.isEqual(prevProps, nextProps);

/* eslint-disable react/no-multi-comp */
const DevicesDialog = React.memo(props => {
  const { dialogState, onCloseDialog } = props;
  const { open, type, value } = dialogState;
  const [addDevice, { data }] = useMutation(ADD_DEVICE);

  const handleCreateNewDevice = async deviceInfo => {
    // console.log(deviceInfo);
    await addDevice({ variables: { deviceInput: deviceInfo } });
    onCloseDialog();
  };

  const onDownloadQrCode = () => {
    fetch(value).then(response => {
      response.blob().then(blob => {
        let url = window.URL.createObjectURL(blob);
        let a = document.createElement('a');
        a.href = url;
        a.download = `${value.split('/').pop()}`;
        a.click();
      });
    });
  };

  const switchDialog = dialogType => {
    switch (dialogType) {
      case 'addNewDeviceForm':
        return (
          <FormDialog
            open={open}
            handleContinue={handleCreateNewDevice}
            handleCancel={onCloseDialog}
            title={'Thm thit b'}
            continueText={'Thc hin'}
            cancelText={'B qua'}
            formData={addNewDeviceForm}
            disableBackdropClick
            // error={localizeError(error)}
            // isLoading={loading}
          />
        );
      case 'viewQrCode':
        return (
          <AlertDialog
            open={open}
            handleCancel={onCloseDialog}
            handleContinue={onDownloadQrCode}
            title={'M QR'}
            continueText={'Ti xung'}
            cancelText={'Quay li'}
            content={<Image src={value} />}
          />
        );
      default:
        return null;
    }
  };

  return switchDialog(type);
}, areEqual);

DevicesDialog.propTypes = {
  dialogState: PropTypes.object,
  onCloseDialog: PropTypes.func
};

export default DevicesDialog;
